import React from 'react';

const StatsBar: React.FC = () => {
  return (
    <div className="container">
      <div className="stats">
        <div className="stats-grid">
          <div className="stat-item">
            <h3>124+</h3>
            <p>Chin dch thnh cng</p>
          </div>
          <div className="stat-item">
            <h3>15,000+</h3>
            <p>Ngi ng h</p>
          </div>
          <div className="stat-item">
            <h3>2,5M SUI</h3>
            <p> quyn gp</p>
          </div>
          <div className="stat-item">
            <h3>100%</h3>
            <p>Minh bch</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StatsBar;