import React, { useState } from 'react';
import { ConnectButton } from '@mysten/dapp-kit';
import { useNavigate, Link } from 'react-router-dom';

const Navbar: React.FC = () => {
  const [searchQuery, setSearchQuery] = useState('');
  const navigate = useNavigate();

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchQuery(e.target.value);
    // TODO: Implement search logic
  };

  const handleCreateCampaign = () => {
    // Chuyn hng sang trang to chin dch
    // Lu : m bo route trong App.tsx l '/create'
    navigate('/create');
  };

  return (
    <nav className="navbar">
      <div className="nav-container">
        {/* Logo dng Link  v trang ch */}
        <Link to="/" className="logo" style={{ textDecoration: 'none' }}>
           CharityChain
        </Link>

        {/* Search Bar */}
        <div className="search-container">
          <span className="search-icon"></span>
          <input
            type="text"
            className="search-input"
            placeholder="Tm kim chin dch..."
            value={searchQuery}
            onChange={handleSearch}
          />
        </div>

        {/* Nav Menu */}
        <ul className="nav-menu">
          {/* ng h Dropdown */}
          <li className="nav-item">
            <button className="nav-link-btn">
              ng h
              <span className="dropdown-icon"></span>
            </button>
            <div className="dropdown-menu">
              <div className="dropdown-item" onClick={() => navigate('/')}>
                <span className="dropdown-item-icon"></span>
                <span>Tt c chin dch</span>
              </div>
              <div className="dropdown-item">
                <span className="dropdown-item-icon"></span>
                <span>Gio dc</span>
              </div>
              <div className="dropdown-item">
                <span className="dropdown-item-icon"></span>
                <span>Y t</span>
              </div>
            </div>
          </li>

          {/* Gii thiu Dropdown */}
          <li className="nav-item">
            <button className="nav-link-btn">
              Gii thiu
              <span className="dropdown-icon"></span>
            </button>
            <div className="dropdown-menu">
              <div className="dropdown-item">
                <span className="dropdown-item-icon"></span>
                <span>V chng ti</span>
              </div>
              <div className="dropdown-item">
                <span className="dropdown-item-icon"></span>
                <span>Cch thc hot ng</span>
              </div>
            </div>
          </li>
        </ul>

        {/* Buttons */}
        <div className="nav-buttons">
          <ConnectButton />
          <button className="btn-primary" onClick={handleCreateCampaign}>
            To chin dch
          </button>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;