import React from 'react';

const Hero: React.FC = () => {
  const handleDonateNow = () => {
    // Scroll to campaigns section
    document.getElementById('campaigns')?.scrollIntoView({ behavior: 'smooth' });
  };

  const handleCreateCampaign = () => {
    // TODO: Navigate to create campaign page
    alert('Tnh nng to chin dch ang c pht trin!');
  };

  return (
    <section className="hero">
      <div className="hero-container">
        <h1>Nn tng thin nguyn minh bch</h1>
        <p>Xy dng trn Sui Blockchain - Mi giao dch u cng khai v khng th thay i</p>
        <div className="hero-buttons">
          <button className="btn-primary" onClick={handleDonateNow}>
            ng h ngay
          </button>
          <button className="btn-secondary" onClick={handleCreateCampaign}>
            To chin dch
          </button>
        </div>
      </div>
    </section>
  );
};

export default Hero;